.portfolio {
    &__content {
        padding: 20px;
        width: 100%;
        &__filter {
            display: inline-block;
            margin: 0 auto;
            border-radius: 25px;
            border: 1px dashed var(--team-main-color);

            li {
                font-size: 16px;
                cursor: pointer;
                display: inline-block;
                position: relative;
                color: var(--team-main-color);
                text-transform: uppercase;
                padding: 12px 20px;
                overflow: hidden;
                transition: all 0.5s ease;
                 
                &.active {
                    background: var(--team-main-color);
                    color: var(----sub-text-color); 
                    font-weight: 600;   
                    border-radius: 25px;          
                }
            }
        }
        &-cards {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-auto-rows: auto;
            gap: 1rem;
            margin-top: 10px;
            
           

            &--item {
                height: 366px;
                cursor: pointer;
                position: relative;
               
                

                 &:hover .overlay {
                     opacity: 1;

              }

                &--img-wrapper {
                    height: 100%;
                    
                    
                    

                    a {
                        height: 100%;
                        

                        img {
                            height: inherit;
                            object-fit: cover;
                            width: 100%;
                            border: 4px solid var(--team-main-color);
                            border-radius: 4px;
                            
                        }
                    }

                }
                .overlay {
                
                    position: absolute;
                        top: 0;
                        right: 0;
                        left: 0;
                        bottom: 0;
                            height: 100%;
                            width: 100%;
                            opacity: 0;
                            transition: 0.5s ease;
                            background-color: var(--team-main-color);
                            border-radius: 4px;
                            

                            div {
                                display: flex;
                                flex-direction: column;
                                justify-content: center;
                                align-items: center;
                                margin-top: 120px;
                                  
                                    text-align: center;
                                    p{
                                        color: var(--team-backgrond-color);
                                            margin: 0px;
                                            font-size: 3rem;
                                            overflow: hidden;                                            
                                            position: relative;
                                            white-space: nowrap;
                                            animation: typingEffect1 1.5s steps (30, end);
                                            text-transform: uppercase;
                                            text-shadow: 1px 1px 2px black
                                          

                                            
                                    }

                                                                      
                                    button {
                                        cursor: pointer;
                                            margin-top: 16px;
                                            padding: 10px 40px;
                                            font-size: 2rem;
                                            background-color: transparent;                                           
                                            border: 1.5px solid #000000;
                                            border-radius: 4px;
                                            font-weight: 500;
                                            text-shadow: 1px 1px 2px black;
                                            
                                            transition: box-shadow 250ms ease-in-out 0s;
                                            &:hover {
                                              
                                            box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
                                            }

                                    }
                            }
                }
                

            }

        }
    }
}

  @keyframes typingEffect1 {
      from {
          width: 0;
      }

      to {
          width: 100%;
      }
  }

@media only screen and (max-width: 480px) {  
  

        .portfolio__content-cards {
            display: grid;
                grid-template-columns: repeat(1, 1fr);
                grid-auto-rows: auto;
                gap: 1rem;
                margin-top: 10px;

        }


       
}

@media only screen and (min-width: 481px) and (max-width: 767px){
   

    .portfolio__content-cards {
        grid-template-columns: repeat(2, 1fr);
        &__item {
            .overlay{
                div>p {                    
                    font-size: 2.5rem;
                }
            }
        }
    }


   
}


